body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.main {
  width: 500px; /* ширина */
  margin: 0 auto; /* центрування блоку */
  padding: 10px 30px;
  border: 4px solid blanchedalmond;
  border-radius: 10px;
  background-color: #d3d9f6;
}

.title {
  font-size: 24px;
  color: salmon;
}

.list {
  list-style: none;
  padding-left: 0;
  font-size: 16px;
}

.task {
  display: flex;
  justify-content: space-between; /* горизонтально - по краям */
  align-items: center; /* вертикально - по центру */
  border-bottom: 1px solid mediumpurple; /* нижня рамка пыд задачею */
}

.btn {
  padding: 10px 20px;
  margin-left: 10px;
  background-color: whitesmoke;
  color: red;
  border: 1px solid fuchsia;
  border-radius: 4px;
  font-size: 14px;

  transition: all 0.3s; /* плавна анімація */
}

.btn:hover {
  background-color: black;
  color: white;
  cursor: pointer;
}

input {
  padding: 10px;
  border: 1px solid hotpink;
  border-radius: 4px;
  font-size: 16px;
}

input::placeholder {
  color: orange;
  letter-spacing: 2px;
}
.task {
  color: red;
  background-color: slategrey;
}
.complete {
  text-decoration: line-through;
  color: black;
}

/* Стилі для мобільних пристроїв, які відрізняються */
@media (max-width: 768px) {
  .main {
    max-width: 80%;
  }

  .run {
    display: none;
  }
}
